<template>
    <div>
        <h1 align="center">QnA 작성</h1>
        <table align="center" class="contentBox">
            <tr>
                <th>제목</th>
                <td><input type="text" name="title" v-model="QInput.title"/></td>
            </tr>
            <tr>
                <th>글쓴이</th>
                <!-- <td>{{LoginUserInfo.id}}</td> -->
                <td></td>
            </tr>
            <tr>
                <th>내용</th>
                <td height="10">
                    <textarea rows="10" cols="200"  name="content" v-model="QInput.content"></textarea>
                </td>
            </tr>
            <tr>
                <td colspan="2" align="right">
                    <input type="button" value="작성하기" @click="insertQItem">
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    export default {
        data() { // ui에서 입력받은 데이터를 저장하기 위한 선언
            return {
                QInput: {
                    title: "",
                    userid: "",
                    content: "",
                }
            };
        },
        created () {
            this.$store.dispatch('getQDetail', {no:this.$route.params.qno});
            this.$store.dispatch('getADetail', {no:this.$route.params.qno});
        },
        methods: {
            insertQItem() {
                this.$store.dispatch('insetQItem', {QInput:this.QInput});
                this.$router.push('/qnalist');
            }
        },
    }
</script>

<style scoped>
.contentBox{
    width: 80%;
    font-size: 1.rem;
}

.contentBox th{
    width: 150px;
    background-color: dimgray;
    color : white;
    border-radius: 5px 0px 0px 5px;
}

.contentBox td{
    border: solid 1px black;
    border-radius: 0px 5px 5px 0px;
}

.content{
    height: 100px;
}

.replyBox{
    width: 80%;
}
</style>