<template>
    <div>
        <h1 align="center">QnA 수정</h1>
        <table align="center" class="contentBox">
            <tr>
                <th>제목</th>
                <td><input type="text" v-model="getQDetail.title"></td>
            </tr>
            <tr>
                <th>글쓴이</th>
                <td>{{getQDetail.userid}}</td>
            </tr>
            <tr>
                <th>작성일</th>
                <td>{{getQDetail.write_date}}</td>
            </tr>
            <tr>
                <th>조회수</th>
                <td>{{getQDetail.hit}}</td>
            </tr>
            <tr>
                <th>내용</th>
                <td height="10">
                    <textarea rows="10" cols="200" v-model="getQDetail.content">
                    </textarea>
                </td>
            </tr>
            <tr v-for="(AItem) in getADetail" v-bind:key="AItem.ano">
                <td>{{AItem.userid}} {{AItem.write_date}}</td>
                <td>{{AItem.content}}</td>
            </tr>
            <tr>
                <td colspan="2" align="right">
                    <input type="button" value="수정하기" @click="updateQItem(getQDetail.qno)">
                    <input type="button" value="삭제하기" @click="deleteQItem(getQDetail.qno)">
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    export default {
        created () {
            this.$store.dispatch('getQDetail', {no:this.$route.params.no});
            this.$store.dispatch('getADetail', {no:this.$route.params.no});
        },
        computed: {
            getQDetail() {
                return this.$store.state.QDetail;
            },
            getADetail() {
                return this.$store.state.ADetail;
            }
        },
        methods: {
            updateQItem(no) {
                console.log('update 처리 되었습니다. 번호 : ' + no);
                this.$store.dispatch('updateQItem', {no, Qupdate:this.$store.state.QDetail});
                this.$router.push('/qnadetail/' + no);
            },
            deleteQItem(no) {
                console.log('delete 처리 되었습니다. 번호 : ' + no);
                this.$store.dispatch('deleteQItem', {no});
                this.$router.push('/qnalist');
            }
        },
    }
</script>

<style scoped>
.contentBox{
    width: 80%;
    font-size: 1.rem;
}

.contentBox th{
    width: 150px;
    background-color: dimgray;
    color : white;
    border-radius: 5px 0px 0px 5px;
}

.contentBox td{
    border: solid 1px black;
    border-radius: 0px 5px 5px 0px;
}

.content{
    height: 100px;
}

.replyBox{
    width: 80%;
}
</style>