<template>
    <div>
        <h1 align="center">QnA 상세내용</h1>
        <table align="center" class="contentBox">
            <input type="hidden" value="getQDetail.qno" v-model="AInput.qno">
            <tr>
                <th>제목</th>
                <td>{{getQDetail.title}}</td>
            </tr>
            <tr>
                <th>글쓴이</th>
                <td>{{getQDetail.userid}}</td>
            </tr>
            <tr>
                <th>작성일</th>
                <td>{{getQDetail.write_date}}</td>
            </tr>
            <tr>
                <th>조회수</th>
                <td>{{getQDetail.hit}}</td>
            </tr>
            <tr>
                <th>내용</th>
                <td class="content">{{getQDetail.content}}</td>
            </tr>
            <tr>
                <td aling="left">
                    <input type="button" value="목록" @click="goQList">
                </td>
                <td align="right">
                    <input type="button" value="수정" @click="goQItemUpdate(getQDetail.qno)">
                    <input type="button" value="삭제" @click="deleteQItem(getQDetail.qno)">
                </td>
            </tr>
        </table>
        <table align="center" class="replyBox">
            <div v-for="(AItem) in getADetail" v-bind:key="AItem.ano">
                <tr> 
                    <td>
                        {{AItem.userid}}
                        <!-- <sapn v-show="AItem.userid == LoginUserInfo.id" @click="deleteAItem(AItem.ano)">삭제</sapn> -->
                    </td>
                    <td> | {{AItem.write_date}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td>{{AItem.content}}</td>
                </tr>
            </div>
        </table>
        <!-- <table v-if="LoginUserInfo.id == 'ssafy' || LoginUserInfo.id == AItem.userid">
            <tr>
                <td>{{LoginUserInfo.id}}</td>
                <td><input type="text"><input type="button" value="답변쓰기" name="acontent" v-model="AInput.content" @click="insertAItem(AItem.ano)"></td>
            </tr>
        </table> -->
    </div>
</template>

<script>
    export default {
        data() { // ui에서 입력받은 데이터를 저장하기 위한 선언
            return {
                AInput: {
                    userid: "",
                    content: "",
                    qno: ""
                }
            };
        },
        created () {
            this.$store.dispatch('getQDetail', {no:this.$route.params.no});
            this.$store.dispatch('getADetail', {no:this.$route.params.no});
        },
        computed: {
            getQDetail() {
                return this.$store.state.QDetail;
            },
            getADetail() {
                return this.$store.state.ADetail;
            }
        },
        methods: {
            goQItemUpdate(no) {
                console.log('QnA Update 페이지 이동 처리 되었습니다. 번호 : ' + no);
                this.$router.push('/qnaupdate/' + no);
            },
            deleteQItem(no) {
                console.log('delete 처리 되었습니다. 번호 : ' + no);
                this.$store.dispatch('deleteQItem', {no});
                this.$router.push('/qnalist');
            },
            insertAItem() {
                this.$store.dispatch('insetAItem', {AInput:this.AInput});
            },
            deleteAItem(no) {
                console.log('delete 처리 되었습니다. 번호 : ' + no);
                this.$store.dispatch('deleteAItem', {no});
            },
            goQList(){
                this.$router.push('/qnalist');
            }
        },
    }
</script>

<style scoped>
.contentBox{
    width: 80%;
    font-size: 1.rem;
}

.contentBox th{
    width: 150px;
    background-color: dimgray;
    color : white;
    border-radius: 5px 0px 0px 5px;
}

.contentBox td{
    border: solid 1px black;
    border-radius: 0px 5px 5px 0px;
}

.content{
    height: 100px;
}

.replyBox{
    width: 80%;
}
</style>